.react-select {
  .react-select__menu {
    max-width: 94%;
    width: 94%;
    right: 3%;
    border-radius: 12px;
    border: 0;
    box-shadow: 1px 2px 7px 1px rgba(0, 0, 0, 0.125);
    transition: all 0.3s cubic-bezier(0.215, 0.61, 0.355, 1) 0s,
      opacity 0.3s ease 0s, height 0s linear 0.35s;
    background-color: $white-bg;
    box-sizing: content-box;
    max-height: 200px;
    position: absolute;
    z-index: 2;
    -webkit-overflow-scrolling: touch;
    padding-bottom: 12px;
    padding-top: 12px;
    margin-bottom: 4px;
    margin-top: 4px;
  }
  .react-select__menu-list {
    max-height: 198px;
    overflow-y: auto;
    padding-bottom: unset;
    padding-top: unset;
    position: relative;
  }
  .react-select__option {
    font-size: 0.8571em;
    padding-top: 0.6rem;
    padding-bottom: 0.6rem;
    margin-top: 5px;
    -webkit-transition: all 150ms linear;
    -moz-transition: all 150ms linear;
    -o-transition: all 150ms linear;
    -ms-transition: all 150ms linear;
    transition: all 150ms linear;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    display: block;
    outline: none;
    color: #292b2c;
    &:first-child {
      margin-top: 0;
    }
  }
  .react-select__menu {
    .react-select__option {
      &,
      &:active {
        background-color: transparent;
      }
      &.react-select__option--is-selected {
        &,
        &.react-select__option--is-focused {
          &,
          &:active {
            background-color: $medium-gray;
          }
        }
      }
      &.react-select__option--is-focused {
        &,
        &:active {
          background-color: $smoke-bg;
        }
      }
      &.react-select__option--is-disabled {
        &,
        &:active {
          color: #cccccc;
          cursor: default;
        }
      }
    }
  }
  .react-select__control {
    border-radius: 4px;
    min-height: 40px;
    height: 40px;
    box-sizing: border-box;
    border-collapse: separate;
    display: table;
    width: 100%;
    &,
    &:hover {
      border: 1px solid $light-gray;
      box-shadow: none;
      background-color: transparent;
    }
    &.react-select__control--is-focused {
      .react-select__dropdown-indicator {
        &:after {
          transform: rotate(180deg);
          top: -4px;
        }
      }
      &,
      &:hover {
        border: 1px solid $primary-color;
        box-shadow: none;
      }
    }
    .react-select__value-container--has-value {
      .react-select__multi-value {
        display: inline-flex;
        border-radius: 4px;
        background-color: transparent;
        & + div:not(.react-select__multi-value) {
          display: inline-flex;
        }
        .react-select__multi-value__label {
          font-size: 0.9em;
        }
      }
      .react-select__single-value {
        top: 48%;
        color: $black-color;
      }
    }
    .react-select__placeholder {
      color: $dark-gray;
      line-height: normal;
      font-size: 0.8571em;
      top: 48%;
    }
    .react-select__value-container,
    .react-select__indicators {
      display: table-cell;
    }
    .react-select__indicators {
      width: 60px;
      padding-right: 10px;
      align-items: flex-end;
      vertical-align: middle;
      text-align: right;
      .react-select__indicator-separator {
        display: none;
      }
      .react-select__indicator {
        padding: 0;
        display: inline-block;
      }
      .react-select__dropdown-indicator {
        &:after {
          transition: all 150ms ease 0s;
          border-color: #999 transparent transparent;
          border-style: solid;
          border-width: 5px 5px 2.5px;
          display: inline-block;
          height: 0;
          width: 0;
          position: relative;
          content: "";
        }
        &:hover:after {
          border-top-color: $link-disabled-color;
        }
        & > * {
          display: none;
        }
      }
      .react-select__clear-indicator {
        &:before {
          content: "\D7";
          display: inline-block;
          font-size: 18px;
          line-height: 1;
          top: 2px;
          position: relative;
          margin-right: 3px;
        }
        & > * {
          display: none;
        }
      }
    }
  }
  &.react-select-primary {
    .react-select__control {
      &.react-select__control--is-focused {
        &,
        &:hover {
          border-color: $primary-color;
        }
      }
      .react-select__value-container--has-value {
        .react-select__single-value,
        .react-select__multi-value__label {
          color: $primary-color;
        }
      }
    }
    .react-select__multi-value {
      border: 1px solid $primary-color;
      .react-select__multi-value__remove {
        color: $light-blue;
        border-left: 1px solid $light-blue;
        &:hover {
          color: $primary-color;
          background-color: transparent;
        }
      }
    }
  }
  &.react-select-success {
    .react-select__control {
      &.react-select__control--is-focused {
        &,
        &:hover {
          border-color: $success-color;
        }
      }
      .react-select__value-container--has-value {
        .react-select__single-value,
        .react-select__multi-value__label {
          color: $success-color;
        }
      }
    }
    .react-select__multi-value {
      border: 1px solid $success-color;
      .react-select__multi-value__remove {
        color: $light-green;
        border-left: 1px solid $light-green;
        &:hover {
          color: $success-color;
          background-color: transparent;
        }
      }
    }
  }
  &.react-select-info {
    .react-select__control {
      &.react-select__control--is-focused {
        &,
        &:hover {
          border-color: $info-color;
        }
      }
      .react-select__value-container--has-value {
        .react-select__single-value,
        .react-select__multi-value__label {
          color: $info-color;
        }
      }
    }
    .react-select__multi-value {
      border: 1px solid $info-color;
      .react-select__multi-value__remove {
        color: $light-azure;
        border-left: 1px solid $light-azure;
        &:hover {
          color: $info-color;
          background-color: transparent;
        }
      }
    }
  }
  &.react-select-warning {
    .react-select__control {
      &.react-select__control--is-focused {
        &,
        &:hover {
          border-color: $warning-color;
        }
      }
      .react-select__value-container--has-value {
        .react-select__single-value,
        .react-select__multi-value__label {
          color: $warning-color;
        }
      }
    }
    .react-select__multi-value {
      border: 1px solid $warning-color;
      .react-select__multi-value__remove {
        color: $light-orange;
        border-left: 1px solid $light-orange;
        &:hover {
          color: $warning-color;
          background-color: transparent;
        }
      }
    }
  }
  &.react-select-danger {
    .react-select__control {
      &.react-select__control--is-focused {
        &,
        &:hover {
          border-color: $danger-color;
        }
      }
      .react-select__value-container--has-value {
        .react-select__single-value,
        .react-select__multi-value__label {
          color: $danger-color;
        }
      }
    }
    .react-select__multi-value {
      border: 1px solid $danger-color;
      .react-select__multi-value__remove {
        color: $light-red;
        border-left: 1px solid $light-red;
        &:hover {
          color: $danger-color;
          background-color: transparent;
        }
      }
    }
  }

  &.react-select-white {
    .react-select__control {
      &.react-select__control--is-focused {
        &,
        &:hover {
          border-color: $white-color;
        }
      }
      .react-select__value-container--has-value {
        .react-select__single-value,
        .react-select__multi-value__label {
          color: $white-color;
        }
      }
    }
    .react-select__multi-value {
      border: 1px solid $white-color;
      .react-select__multi-value__remove {
        color: $white-color;
        border-left: 1px solid $white-color;
        &:hover {
          color: $white-color;
          background-color: transparent;
        }
      }
    }
  }

  .react-select__multi-value {
    border: 1px solid $dark-gray;
    .react-select__multi-value__remove {
      position: relative;
      color: rgba(222, 222, 222, 0.8);
      border-left: 1px solid rgba(222, 222, 222, 0.8);
      &:hover {
        color: $dark-gray;
        background-color: transparent;
      }
    }
  }
  &.react-select--is-disabled {
    .react-select__multi-value--is-disabled {
      .react-select__multi-value__remove {
        display: none;
      }
      &.react-select__multi-value {
        background-color: #fcfcfc;
        border: 1px solid #e3e3e3;
      }
    }
    .react-select__control--is-disabled {
      background-color: #f9f9f9;
    }
  }
}
