/*          Changes for small display      */

@media (max-width: 767px) {
  #cd-vertical-nav {
    display: none !important;
  }
  .coloured-cards {
    .card-big-shadow {
      margin: 0 auto;
    }
  }

  .register-page {
    .page-header {
      max-height: 2000px;
    }
  }

  .section-testimonials {
    .our-clients {
      margin-top: 0 !important;
    }
  }
  .navbar-transparent {
    // padding-top: 15px;
    background-color: rgba(0, 0, 0, 0.45);
  }
  .register {
    .page-header {
      max-height: fit-content;
